let changelook = ['所有人可见', '仅自己可见'];
import uniNavBar from "../../components/uni-nav-bar/uni-nav-bar";
import uploudImages from "../../components/common/uploud-images/uploud-images";
import uniPopup from "../../components/uni-popup/uni-popup.vue";
export default {
	data() {
		return {
			yinsi:"所有人可见",
			text:"", //文本框
			imageList:[], //图片存储
			showpopup:true, //弹出框
			isget:false, //是否保存
		}
	},
	methods: {
		back(){
			//返回
			uni.navigateBack({
				delta:1
			})
		},
		submit(){
			// 发布
			console.log('发布')
		},
		changelook(){
			// 隐私
			uni.showActionSheet({
			    itemList:changelook ,
			    success:(res) =>{
			        this.yinsi = changelook[res.tapIndex]
			    }
			});
		},
		uploud(arr){
			this.imageList= arr;
		},
		hidePopup(){
			//隐藏弹出框
			this.showpopup = false
		},
		baocun(){
			//是否保存草稿
			uni.showModal({
				content:"是否要保存草稿",
				cancelText:"不保存",
				confirmText:"保存",
				success:res=> {
					if(res.confirm){
						//保存
						
					} else {
						//不保存
					}
					this.isget = true
					uni.navigateBack({
						delta:1
					})
				}
			})
		}
	},
	onBackPress() {
		//是否有值
		if(!this.text && this.imageList.length < 1){return}
		// 监听用户返回
		if(!this.isget){
			this.baocun()
			return true
		}
		
	},
	components:{
		uniNavBar,
		uploudImages,
		uniPopup
	}
}
